import { PageType } from "./types";

export default class QueryUtils {//
    public static getPageType(): PageType{//
        //"page"
        let result: PageType
        const pageQuery:string|null = QueryUtils.getQuery("page")
        if (//queryPageType
            pageQuery === null ||
            pageQuery === "login" ||
            pageQuery === "register" ||
            pageQuery === "timetable" ||
            pageQuery === "task" ||
            pageQuery === "shift" ||
            pageQuery === "event" ||
            pageQuery === "calendar"
        ) {
            result = pageQuery as PageType
            return result
        } else {
        // 
            throw new Error("page")
        }
    }

    private static getQuery(paramName: string): string | null {//(URL)
        const pageUrl=window.location.href//
        paramName=paramName.replace(/[\[\]]/g,"\\$&");
        const regex=new RegExp("[?&]"+paramName+"(=([^&#]*)|&|#|$)"),
        results=regex.exec(pageUrl)
        if(!results)return null
        if(!results[2])return ""
        return decodeURIComponent(results[2].replace(/\+/g," "))
    }

    public static matchQuery(paramName: string, value: string | null){//
        const pageQuery: string | null = QueryUtils.getQuery(paramName)
        if(pageQuery === value){
            return true
        }else{
            return false
        }
    }
}