/* firebase */
export type UserInfo = {
    email:string,
    password:string
}

/* SendGrid */
export type EmailData = {
    to: string;
    bcc?: string;
    from?: string;
    subject: string;
    text: string;
}

/*  */
export type ContentType = "timetable"|"task"|"shift"|"event"
export type PageType = null|"login"|"register"|TabType//nullindex()
export type TabType = ContentType|"calendar"

/*  */
export type LoginData = {
    email: string;
    password: string;
}
export type RegisterData = {
    email: string;
    password: string;
    passwordCheck: string;
}

/*  */
export type Timetable = {
    id?: string;
    title: string;
    teacher: string;
    classroom: string;
    startTime: number;
    endTime: number;
    dayOfWeek: number; // Date.getDay()
};

export interface Timetables{
    [id: string]: Timetable;
}

export type TimetableList = {
    timeTables: Timetables;
};

/*  */
export type Task = {
    id?: string;
    title: string;
    description: string;
    deadline: number; // 
};

export interface Tasks{
    [id: string]: Task;
}

export type TaskSettings = {
    enabledAlert?: boolean; // /
    daysBeforeDeadline?: number; // 
    autoTaskDelete?: boolean;
};

export type TaskList = {
    tasks: Tasks;
    taskSettings: TaskSettings;
};


/*  */
export type Shift = {
    id?: string;
    startTime: number; // 
    endTime: number; // 
    breakTime: number; // 
};

export interface Shifts{
    [id: string]: Shift;
}

export type ShiftList = {
    shifts: Shifts;
};

/*  */
export type Event = {
    id?: string;
    title: string;
    description: string;
    startTime: number;
    endTime: number;
    isAllDay: boolean; // 
};

export interface Events{
    [id: string]: Event;
}

export type EventSettings = {
    hidePassedEvent?: boolean; // 
};

export type EventList = {
    events: Events;
    eventSettings: EventSettings;
};

/*  */
export type Weekday = { // 
    sun: boolean,
    mon: boolean,
    tue: boolean,
    wed: boolean,
    thu: boolean,
    fri: boolean,
    sat: boolean
}

