import {
    createUserWithEmailAndPassword,
    sendEmailVerification ,
    signInWithEmailAndPassword,
    sendPasswordResetEmail,
    Auth,
    getAuth,
    signOut,
    onAuthStateChanged,
    User
} from 'firebase/auth'
import { FirebaseError } from '@firebase/util'
import { IndexPageUrl, LoginPageUrl } from "./constants"
import { UserInfo } from "./types"
import QueryUtils from "./QueryUtils"

export default class AppUser {
    public static uid: string|null = null;
    private static userInfo: UserInfo = {
        email:"",
        password:""
    } as UserInfo;

    public static setUserInfo(email: string, password: string){//userInfo
        const userInfo:UserInfo={} as UserInfo
        userInfo.email=email
        userInfo.password=password
        AppUser.userInfo=userInfo
    }

    public static async assignUserInfo():Promise<void>{ // id
        const authState: User | null = await AppUser.getAuthState()
        if(authState){
            AppUser.uid = authState.uid
            if(authState.email){
                AppUser.userInfo.email=authState.email
            }
        }
    }

    public static async signUp(redirectLink?: string):Promise<void>{//
        try {
            const auth: Auth = getAuth()
            const userCredential = await createUserWithEmailAndPassword(
                auth,
                AppUser.userInfo.email,
                AppUser.userInfo.password
            )
            await sendEmailVerification(userCredential.user)
            console.log("email sended")
            window.alert("")
            if(redirectLink){
                location.href = redirectLink
            }
        } catch (e) {
            if (e instanceof FirebaseError) {
                console.log(e)
                window.alert("\n")
            }
        }
    }

    public static async signIn(redirectLink?: string):Promise<void>{//
        try {
            const auth: Auth = getAuth()
            await signInWithEmailAndPassword(
                auth,
                AppUser.userInfo.email,
                AppUser.userInfo.password
            )
            if(redirectLink){
                location.href = redirectLink
            }
        } catch (e) {
            if (e instanceof FirebaseError) {
                console.log(e)
                window.alert("\n")
            }
        }
    }

    public static async signOut(redirectLink?: string):Promise<void>{//
        try {
            const auth: Auth = getAuth()
            await signOut(auth)
            if(redirectLink){
                location.href = redirectLink
            }
        } catch (e) {
            if (e instanceof FirebaseError) {
                console.log(e)
            }
        }
    }

    public static resetEmail(email: string): void{ // 
        const auth: Auth = getAuth()
        sendPasswordResetEmail(auth, email); // Email
    }

    private static async getAuthState():Promise<User | null>{ // 
        return new Promise((resolve, reject) => {
            const auth: Auth = getAuth();
            onAuthStateChanged(auth, (user) => {
                if (user) {
                    // User is signed in, see docs for a list of available properties
                    // https://firebase.google.com/docs/reference/js/firebase.User
                    const uid = user.uid;
                    resolve(user);
                } else {
                    // User is signed out
                    resolve(null);
                }
            });
        });
    }

    public static async redirect():Promise<void>{ // 
        const authState: User | null  = await AppUser.getAuthState()
        const isLoginContent: boolean = QueryUtils.matchQuery("page","login")
        const isRegisterContent: boolean = QueryUtils.matchQuery("page","register")
        if(authState && (isLoginContent || isRegisterContent)){///
            location.href=IndexPageUrl//
        }else if(!authState && !(isLoginContent || isRegisterContent)){//
            location.href=LoginPageUrl
        }else{
            //
            //uid
            await AppUser.assignUserInfo()
        }
    }
}